<template>
    <section>
        <div id="ExpenseDetail">
            <div class="cont_99P marginbottom5 form-group pt-3 px-1">
                <select id="ddlTotalTimeExpense" class="form-control width_48 col-md-6 px-1 float-left mb-2">
                    <option value="today">Today</option>
                    <option value="yesterday">Yesterday</option>
                    <option value="thisweek">ThisWeek</option>
                    <option value="last7days">Last7days</option>
                    <option value="thismonth">ThisMonth</option>
                    <option value="last30days">Last30days</option>
                    <option value="lastmonth">LastMonth</option>
                </select>
                <select id="ddlStatusExpense" class="width_48 form-control pullright arabicpulleft col-md-6 px-1 float-left mb-2">
                    <option value="Approved">Approved</option>
                    <option value="Pending">Pending</option>
                </select>
            </div>
            <div id="Projects" class="dashboard cont_99P px-1" >
                <ul class="newsep">
                    <li v-if="approvalTypeForTime=='Yes'" class="list-group-item d-flex justify-content-between">
                        <span class="timelabel">Amount</span><span id="lblExpenseAmount" class="timelabelright arabicpulleft">{{ TOTALEXPENSE }}</span>
                    </li>
                    <li v-else class="list-group-item d-flex justify-content-between">
                        <span class="timelabel">Amount</span><span id="lblExpenseAmount" class="timelabelright arabicpulleft">{{TOTALEXPENSE}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        widgetInfo: null,
        moduleName: null,
        fromDate: null,
        toDate: null
    },
    data(){
        
        return{
            approvalTypeForTime:'',
            TOTALEXPENSE:''
        }
    },
    created:function(){
        this.ExpenseDetail();
    },
    methods:{
        ExpenseDetail:function(){            
            var url=`requestType=&statusType=`
            DataService.ExpenseDetail(url).then(response=>{
                debugger
                this.TOTALEXPENSE=response.data[0].TOTALEXPENSE;
                this.GetConfigurationValueKey('PMCanSeeProjectFinance');
            })
        },
        GetConfigurationValueKey: function (ths) {
            debugger
            var url = `ConfigurationValueByKey=${ths}`;
            DataService.GetConfigurationValue(url).then(response => {
                debugger
                this.approvalTypeForTime = response.data.CONFIG_DATA_VALUE;
            });
        }
    }
}
</script>